import React, { useState } from "react";
import styled, { keyframes } from 'styled-components';

import StickerImg1 from "../../../res/Sticker_App_Cen_01.webp";
import { Header } from "../../PrimaryStyles";

const Content = [
    {
        src: StickerImg1,
        title: "Bulk Image Manipulation Windows Application",
        list: [
            "Resizes, renames, and converts image format in bulk with a friendly graphical user interface.",
            "Generates a json file of the output for easy integration with Whatsapp Stickers Android App.",
            "Frontend is written in Python using PyQt4.",
            "Backend is written in Python with python library Pillow."
        ]
    },
    {
        src: StickerImg1,
        title: "REST Java API Server for Neo4j Graph Database (Course Project)",
        list: [
            "Goal to compute the bacon number of an actor",
            "Supports Basic CRUD operations (getActor, addActor, ...)",
            "Takes advantage of multiple design patter such as the Bridge design pattern and the Singeleton design pattern."
        ]
    },
    {
        src: StickerImg1,
        title: "WAHWAH",
        list: []
    },
    {
        src: StickerImg1,
        title: "WAHWAHWAH",
        list: []
    }
]

const ProjectInstanceDiv = styled.div`
    flex-grow: 1;
    height: auto;
    width: 100%;
    display: grid;
    grid-template-columns: [a] 1fr [b] 1fr [c] 1fr [d];
    grid-template-rows: [a] auto [b] 1fr [c];
    column-gap: 5%;
    @media (max-width: 1000px) {
        grid-template-rows: [a] 4rem [b] 90vw [c] auto [d];
    }
`

const Image = styled.img`
    height: 100%;
    width: 100%;
    grid-column: b / d;
    @media (max-width: 1000px) {
        grid-column: a / d;
        grid-row: b / c;
    }
`

const ProjectH1 = styled(Header)`
    grid-column: a / d;
    border-bottom: none;
`
const ProjectInfoList = styled.ul`
    font-size: larger;
    grid-column: a / b;
    grid-row: b / c;
    @media (max-width: 1000px) {
        grid-column: a / d;
        grid-row: c / d;
    }
`

const ProjectDiv = styled.div`
    grid-column: b / f;
    z-index: -1;

    transform: translate(${props => -props.page * 120}%);
    
    transition: transform 0.25s ease;

    display: grid;
    grid-template-columns: repeat(4, 100%);
    grid-template-rows: auto;
    column-gap: 20%;
    @media (max-width: 1000px) {
        grid-column: a / g;
    }
`


const ProjectInstance = ({item}) => {
    return (
        <ProjectInstanceDiv>
            <ProjectH1>{item.title}</ProjectH1>
            <Image src={item.src} ></Image>
            <ProjectInfoList>
                {item.list.map((item, i) => <li key={i}>{item}</li>)}
            </ProjectInfoList>
        </ProjectInstanceDiv>
    );
}

const Project = ({pos, ChangeProject, page}) => {

    const [touchStart, setTouchStart] = useState(null)
    const [touchEnd, setTouchEnd] = useState(null)

    // the required distance between touchStart and touchEnd to be detected as a swipe
    const minSwipeDistance = 50 

    const onTouchStart = (e) => {
        setTouchEnd(null) // otherwise the swipe is fired even with usual touch events
        setTouchStart(e.targetTouches[0].clientX)
    }

    const onTouchMove = (e) => setTouchEnd(e.targetTouches[0].clientX)

    const onTouchEnd = () => {
        if (!touchStart || !touchEnd) return
        const distance = touchStart - touchEnd
        const isLeftSwipe = distance > minSwipeDistance
        const isRightSwipe = distance < -minSwipeDistance
        if (isLeftSwipe || isRightSwipe) console.log('swipe', isLeftSwipe ? 'left' : 'right')
        if (isLeftSwipe) ChangeProject(null, "left")
        if (isRightSwipe) ChangeProject(null, "right")
    }

    return (

        <ProjectDiv page={page} onTouchStart={onTouchStart} onTouchMove={onTouchMove} onTouchEnd={onTouchEnd}>
            {Content.map(item => <ProjectInstance item={item} key={item.title}/>)}
        </ProjectDiv>

    );
}

export default Project;